.hero {
  background-color: #f9f3f2;
  height: 774px;
  padding-top: 103px;
  padding-bottom: 72px;
}

.container {
  width: 1200px;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
  position: relative;
}

.heroTitle {
  color: #977a58;
  font-family: 'Playfair Display';
  font-size: 70px;
  font-style: normal;
  font-weight: 400;
  line-height: 120%;
  width: 490px;
  padding-top: 50px;
}

.heroInfo {
  color: #977a58;
  font-family: Garamond;
  font-size: 22px;
  font-style: normal;
  font-weight: 400;
  line-height: 28px;
  width: 392px;
  margin-top: 20px;
}

.heroBtn {
  border-radius: 15px;
  background: #ffc1c5;
  border: none;
  display: flex;
  width: 185px;
  height: 55px;
  padding: 6px;
  justify-content: center;
  align-items: center;
  color: #977a58;
  font-family: Garamond;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 112.528%;
  margin-top: 32px;
  cursor: pointer;
  transition: all 250ms ease;
}

.heroBtn:hover {
  background-color: #f8bec2;
}

.heroBtnDecor {
  width: 173px;
  height: 43px;
  border-radius: 15px;
  border: 1px solid #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}

.heroBubblesTitle {
  position: absolute;
  top: 0;
  left: 300px;
}

.heroPhotoTitle {
  position: absolute;
  top: 0;
  left: 530px;
  border-top-left-radius: 250px;
  border-top-right-radius: 250px;
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
}

.heroSocialNetworks {
  display: flex;
  gap: 7px;
  margin-top: 106px;
}

.heroSocialNetworkItem {
  color: #977a58;
}

@media (max-height: 710px) {
  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .hero {
    height: 600px;
  }
  .heroTitle {
    width: 700px;
    text-align: center;
    font-size: 60px;
    font-weight: 400;
    position: relative;
    z-index: 1;
  }

  .heroInfo {
    width: 392px;
    text-align: center;
    position: relative;
    z-index: 1;
  }

  .heroPhotoTitle {
    display: none;
  }

  .heroBubblesTitle {
    z-index: 0;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(10deg);
  }

  .heroBtn {
    position: relative;
    z-index: 1;
  }

  .heroSocialNetworks {
    margin-top: 40px;
    position: relative;
    z-index: 1;
  }
}
